.App {
  text-align: center;
  background-color: #eee;
  padding: 30px 0;
  height: 100%;
  width: 100%;
  display: inline-block;
}

.App-logo {
  margin-bottom: 30px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.task-wrapper {
  width: 50%;
  text-align: center;
  margin: 0 auto;
  padding: 0 20px;
  display: block;
  height: 100%;
}

.task-container {
  padding: 10px 20px;
  border-bottom: 1px solid #eee;
  float: left;
  width: 100%;
  background-color: #fff;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0,0,0,0.15);
          box-shadow: 0 1px 3px 0 rgba(0,0,0,0.15);
}

.task-title,
.task-button {
  display: inline-block
}

.task-title a {
  text-decoration: none;
  color: #151b26
}

.task-title {
  float: left;
  font-size: 13px;
  margin-top: 1.25em;
}

.init-button {
  float: none;
  margin-bottom: 20px;
}

.task-button {
  float: right;
}

.init-button,
.task-button {
  color:#14aaf5;
  border: 1px solid #14aaf5;
  background-color: transparent;
  padding: 5px 10px;
  border-radius: 3px;
  margin-top: 0.75em;
  margin-bottom: 1em;
}

.init-button:hover,
.task-button:hover {
  cursor: pointer;
  border-color: #fff;
  -webkit-box-shadow: 0 0 0 3px #32c1ff;
          box-shadow: 0 0 0 3px #32c1ff;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



